<template>
  <v-btn
    class="cancel mr-auto"
    :class="{ 'disable-events': isDisabled }"
    color="#FF7141"
    min-height="29px"
    ref="cancel"
    small
    dark
    depressed
    @click="cancelChange"
    :ripple="false"
  >
    Отмена
  </v-btn>
</template>

<script>
export default {
  name: 'CancelButton',
  data() {
    return {
      isDisabled: true
    };
  },
  methods: {
    cancelChange() {
      let btnRecalc = document.querySelector('.recalc'),
        btnSave = document.querySelector('.save'),
        btnCancel = document.querySelector('.cancel');

      btnRecalc.classList.add('disable-events');
      btnSave.classList.add('disable-events');
      btnCancel.classList.add('disable-events');
    }
  }
};
</script>

<style scoped lang="sass">
button.cancel
  width: 120px
  padding-top: 2px !important
  font: 14px/17px $body-font-family
  letter-spacing: 0.01px
  text-transform: none

.disable-events
  pointer-events: none
  opacity: 0.5
</style>
