<template>
  <div>
    <p class="filter-label">Район управления ДРЛ</p>
    <v-select
      v-model="selected"
      :items="drls"
      color="#FF7141"
      item-text="name"
      item-value="id"
      solo
      flat
      hide-details
      @change="$emit('change-drl', selected)"
    >
      <template #item="{ item }">
        <span class="select-item-text">{{ item.name }}</span>
      </template>
    </v-select>
  </div>
</template>

<script>
import axios from 'axios';
export default {
  name: 'DrlFilter',
  created() {
    this.fetchDrl();
  },
  methods: {
    fetchDrl() {
      axios.get('../../server/drl.json').then((response) => {
        this.drls = response.data.data;
      });
    }
  },
  data() {
    return {
      selected: 1,
      drls: []
    };
  }
};
</script>
