.theme--light
  .v-input
    width: 260px
    font-size: 16px
    border: 1px solid #7387A0
    transition: border-color $transition-fast
    &:hover
      border-color: $color-dark-blue

.filter-label
  margin-bottom: 7px
  font: 16px/18px $font-bold
  color: $color-dark-blue

.select-item-text
  color: $color-dark-blue
</style>
