<template>
  <v-expansion-panels v-model="panel" focusable flat>
    <v-expansion-panel>
      <v-expansion-panel-header
        @click="changeFilter()"
        hide-actions
        :ripple="false"
      >
        <span class="mr-2 icon icon-funnel"></span>
        <span>Фильтры</span>
        <span class="icon icon-arrow-dropdown"></span>
      </v-expansion-panel-header>
      <v-expansion-panel-content>
        <v-row dense>
          <DateFilter v-if="filters.dateFilter" />
          <DrlFilter v-if="filters.drlFilter" />
        </v-row>
        <FilterButtons />
      </v-expansion-panel-content>
    </v-expansion-panel>
  </v-expansion-panels>
</template>

<script>
import DateFilter from './DateFilter';
import DrlFilter from './DrlFilter';
import FilterButtons from './FilterButtons';

export default {
  name: 'Filters',
  components: {
    DateFilter,
    DrlFilter,
    FilterButtons
  },
  props: {
    filters: Object
  },
  data() {
    return {
      panel: 0
    };
  },
  methods: {
    changeFilter() {
      this.$store.state.filterOpen = !this.$store.state.filterOpen;
    }
  },
  created() {
    this.$store.state.filterOpen = true;
  }
};
</script>

