      font-size: 12px
      height: 32px
      line-height: 14px
      border-color: $color-light-grey
      border-style: solid
      border-left-width: 0
      border-right-width: 0
      border-top-width: 0
      border-bottom-width: 1px
      div.detail-cell::after
        display: block
        position: absolute
        content: ''
        top: 7%
        left: 7%
        height: 85%
        width: 85%
        z-index: 10
        border: 1px solid $color-blue-links
        border-radius: $common-radius
        cursor: pointer
    td.numbers-font-size
      font-size: 14px
      line-height: 16px
    td.back-pink
      background-color: $color-pink
    td.back-yellow
      background-color: $color-light-yellow
    td.edit-cell-main::before,
    td.edited-cell-main::before
      font-family: $custom-icon-font
      color: $edit-triangle-blue
      content: "\e94f"
      position: absolute
      font-size: 8px
      top: -4px
      left: 0
    td.edited-cell-main::before
      color: $color-blue-links
    td.detail-cell-main
      cursor: pointer
      color: $color-blue-links
    td.bold-text
      font-family: $font-bold
    td.fixed-border
      border-right-width: 1px
    td div
      margin-top: 3px
    th
      border-color: $color-light-grey
      white-space: pre
      font-size: $font-size-root
      line-height: $line-height-root
      font-weight: normal
      color: $color-dark-blue
      border-style: solid
      border-left-width: 0
      border-right-width: 1px
      border-top-width: 0
      position: sticky
      padding: 8px 4px
    th.align-text-left
      text-align: left
    th.smaller-text
      font-size: 12px
      line-height: 14px
  tbody tr:nth-of-type(even)
    background-color: rgba(210, 218, 231, 0.25)

.v-sheet.v-card
  border-radius: 0 0 $common-radius $common-radius
.thick-border-bottom
  border-bottom-width: 3px
.thin-border-bottom
  border-bottom-width: 1px
.theme--light.v-data-table > .v-data-table__wrapper > table > thead > tr:last-child > th
  border: none

.v-text-field
  margin: 0
  font-size: 14px
  ::v-deep
    .v-input__slot
      padding: 0
      &::before
        border-style: none !important
      input
        color: $color-dark-blue
        .edit-cell-main &
          color: $color-blue-links
</style>
