<template>
  <v-btn
    class="recalc mr-5"
    :class="{ 'disable-events': isDisabled }"
    color="#4D6C99"
    min-height="29px"
    ref="recalc"
    small
    dark
    depressed
    @click="recalcData"
    :ripple="false"
  >
    Пересчитать
  </v-btn>
</template>

<script>
export default {
  name: 'RecalcButton',
  data() {
    return {
      isDisabled: true
    };
  },
  methods: {
    recalcData() {
      let btnRecalc = document.querySelector('.recalc'),
        btnSave = document.querySelector('.save');

      btnRecalc.classList.add('disable-events');
      btnSave.classList.remove('disable-events');
    }
  }
};
</script>

<style scoped lang="sass">
button.recalc
  width: 120px
  padding-top: 2px !important
  font: 14px/17px $body-font-family
  letter-spacing: 0.01px
  text-transform: none

.disable-events
  pointer-events: none
  opacity: 0.5
</style>
