<template>
  <v-btn
    class="save mr-5"
    :class="{ 'disable-events': isDisabled }"
    color="#4D6C99"
    min-height="29px"
    ref="save"
    small
    dark
    depressed
    @click="saveData"
    :ripple="false"
  >
    Сохранить
  </v-btn>
</template>

<script>
export default {
  name: 'SaveButton',
  data() {
    return {
      isDisabled: true
    };
  },
  methods: {
    saveData() {
      let btnCancel = document.querySelector('.cancel'),
        btnSave = document.querySelector('.save');

      btnCancel.classList.add('disable-events');
      btnSave.classList.add('disable-events');
    }
  }
};
</script>

<style scoped lang="sass">
button.save
  width: 120px
  padding-top: 2px !important
  font: 14px/17px $body-font-family
  letter-spacing: 0.01px
  text-transform: none

.disable-events
  pointer-events: none
  opacity: 0.5
</style>
